@import "./src/assets/sass/prepends.scss";

  $bg: #fff;
  $fill:#41b883;
  .spinner{
    display: flex;
    justify-content: center;
    align-items: center;
    * {
      line-height: 0;
      box-sizing: border-box;
    }
  }
  .hourglass{
    display:block;
    background:$bg;
    width:32px;
    height:64px;
    box-shadow:
            inset $bg 0 0 0 0,
            inset $fill 0 32px 0 0,
            inset $bg 0 0 64px 0;
    animation:hourglass 1s linear infinite;
  }

  .outer{
    fill:$fill;
  }

  .middle{
    fill:$bg;
  }

  @keyframes hourglass{
    0%{
      transform:rotate(0deg);
      box-shadow:
              inset $bg 0 0 0 0,
              inset $fill 0 32px 0 0,
              inset $bg 0 64px 0 0,
              inset $fill 0 64px 0 0;
    }
    80%{
      transform:rotate(0deg);
      box-shadow:
              inset $bg 0 32px 0 0,
              inset $fill 0 32px 0 0,
              inset $bg 0 32px 0 0,
              inset $fill 0 64px 0 0;
    }
    100%{
      transform:rotate(180deg);
      box-shadow:
              inset $bg 0 32px 0 0,
              inset $fill 0 32px 0 0,
              inset $bg 0 32px 0 0,
              inset $fill 0 64px 0 0;
    }
  }
